methods: {

select_click(val) {
this.current_select = val["value"];
this.isEdit = false;
this.all_select = false;
},

edit_click() {
this.isEdit = this.isEdit == true ? false : true;
this.all_select = false;
},

delete_data() {
this.$store.commit({
type: "set_confirm_data",
data: {
value: true,
title: "",
content: "确定删除么",
fn: () => {

console.log("====================================");
console.log(this.delete_data_rest());
console.log("====================================");
}
}
});
},

delete_data_rest() {
let new_arr = [];
if (this.current_select == 1) {

this.collectionData.map((v, i) => {
if (v["checked"] == true) {
new_arr.push({
id: v["id"]
});
}
});
}
if (this.current_select == 2) {

this.historyData.map((v, i) => {
if (v["checked"] == true) {
new_arr.push({
id: v["id"]
});
}
});
}
return new_arr;
}
},

watch: {

all_select() {
if (this.current_select == 1) {

if (this.all_select == true) {
this.collectionData.map((v, i) => {
v["checked"] = true;
});
} else {
this.collectionData.map((v, i) => {
v["checked"] = false;
});
}
}
if (this.current_select == 2) {

if (this.all_select == true) {
